	bool Sdl_Window::initWindow(int width, int height, const char* windowName)
	{
		_width = width;
		_height = height;

		SDL_Init(SDL_INIT_EVERYTHING);

#if 0
		//SDL_GL_SetAttribute(SDL_GL_CONTEXT_FLAGS, SDL_GL_CONTEXT_FORWARD_COMPATIBLE_FLAG);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_PROFILE_MASK, SDL_GL_CONTEXT_PROFILE_ES);
		// SDL_GL_SetAttribute(SDL_GL_CONTEXT_PROFILE_MASK, SDL_GL_CONTEXT_PROFILE_CORE);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_MAJOR_VERSION, 3);
        SDL_GL_SetAttribute(SDL_GL_CONTEXT_MINOR_VERSION, 0);
		SDL_GL_SetAttribute(SDL_GL_DEPTH_SIZE, 24);
		SDL_GL_SetAttribute(SDL_GL_STENCIL_SIZE, 8);
		SDL_GL_SetAttribute(SDL_GL_DOUBLEBUFFER, 1);
#else
#ifndef __EMSCRIPTEN__
		// SDL_GL_SetAttribute(SDL_GL_CONTEXT_FLAGS, SDL_GL_CONTEXT_FORWARD_COMPATIBLE_FLAG);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_FLAGS, SDL_GL_CONTEXT_PROFILE_CORE);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_PROFILE_MASK, SDL_GL_CONTEXT_PROFILE_CORE);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_MAJOR_VERSION, 3);
        SDL_GL_SetAttribute(SDL_GL_CONTEXT_MINOR_VERSION, 3);
		SDL_GL_SetAttribute(SDL_GL_DEPTH_SIZE, 24);
		SDL_GL_SetAttribute(SDL_GL_STENCIL_SIZE, 8);
		SDL_GL_SetAttribute(SDL_GL_DOUBLEBUFFER, 1);
#else
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_MAJOR_VERSION, 2);
		SDL_GL_SetAttribute(SDL_GL_CONTEXT_MINOR_VERSION, 0);
		SDL_GL_SetAttribute(SDL_GL_DEPTH_SIZE, 16);

		SDL_GL_SetAttribute(SDL_GL_RED_SIZE, 2);
		SDL_GL_SetAttribute(SDL_GL_GREEN_SIZE, 3);
		SDL_GL_SetAttribute(SDL_GL_BLUE_SIZE, 2);
		SDL_GL_SetAttribute(SDL_GL_ALPHA_SIZE, 2);
#endif
#endif

		//SDL_GL_SetAttribute(SDL_GL_DOUBLEBUFFER, 1);


		Uint32 flags = SDL_WINDOW_OPENGL | SDL_WINDOW_RESIZABLE;

#ifdef __EMSCRIPTEN__
		_window = SDL_CreateWindow(windowName, 0, 0, width, height, flags);
#else
		_window = SDL_CreateWindow(windowName, SDL_WINDOWPOS_CENTERED, SDL_WINDOWPOS_CENTERED, width, height, flags);
#endif

		if (!_window)
		{
			// debugBreak();
			printf("failed to window \n");
			std::terminate();
			return false;
		}

		SDL_GLContext glContex = SDL_GL_CreateContext(_window);

		// auto rdr = SDL_CreateRenderer(wnd, -1, SDL_RENDERER_ACCELERATED | SDL_RENDERER_TARGETTEXTURE);

		if (!glContex)
		{
			// debugBreak();
			printf("failed to context \n");
			std::terminate();
			return false;
		}


		SDL_GL_MakeCurrent(_window, glContex);
		SDL_GL_SetSwapInterval(0);

#ifndef __EMSCRIPTEN__
		if (!gladLoadGL())
		{
			printf("failed to initaliaze GLAD\n");
			// debugBreak();
			std::terminate();
			return false;
		}
#endif




#if 0
		EMSCRIPTEN_WEBGL_CONTEXT_HANDLE context;

		EmscriptenWebGLContextAttributes attrs;
		emscripten_webgl_init_context_attributes(&attrs);
		attrs.majorVersion = 2;
		attrs.minorVersion = 0;
		EMSCRIPTEN_WEBGL_CONTEXT_HANDLE context = emscripten_webgl_create_context(0, &attrs);
		EMSCRIPTEN_RESULT res = emscripten_webgl_make_context_current(context);
#endif




		printf("*** OpenGL Version: %s  ***\n", glGetString(GL_VERSION));

		glViewport(0, 0, width, height);
		glEnable(GL_BLEND);
		glBlendFunc(GL_SRC_ALPHA, GL_ONE_MINUS_SRC_ALPHA);
		glClearColor(114.f / 255.0f, 144.f / 255.0f, 154.f / 255.0f, 1.0f);

		return true;
	}

	bool Sdl_Window::updateMessages()
	{
		return true;
	}

	void Sdl_Window::swap()
	{
		SDL_GL_SwapWindow(_window);
		
		// eglSwapBuffers(0, 0);
	}
