	vec2f Input::getMousePosition()
	{
		return *_mousePos;
	}

	vec2f Input::getMousePositionWorld(EngineCore* core)
	{
		return camera::convertScreenToWorld(core, Input::getMousePosition());
	}

	bool Input::mouseDown(Mouse button)
	{
		return mouseDown((int)button);
	}

	bool Input::mouseDown(int numButton)
	{


		if (numButton >= 0 && numButton < maxMouseButtons && !_hovered)
			return _mouseButtons[numButton];

		// ASSERT(false);
		return false;
	}

	bool Input::mousePressed(Mouse button)
	{
		int b{ (int)button };
		if (b >= 0 && b < maxMouseButtons && !_hovered)
		{
			return _mouseButtons[b] && !_mouseButtonsLastFrame[b];
		}
		return false;
	}

	bool Input::isKeyPressed(Keycode keycode_)
	{
		int keycode = (int)keycode_;
		// printf("%i : %i : %i", keycode_, _thisFrame[keycode], _lastFrame[keycode]);
		if (keycode >= 0 && keycode < maxKeyboardButtons)
			return _thisFrame[keycode] && !_lastFrame[keycode];

		// ASSERT(false); // no such key
		return false;
	}

	bool Input::isKeyDown(Keycode keycode_)
	{
		int keycode = (int)keycode_;
		if (keycode >= 0 && keycode < maxKeyboardButtons)
			return _thisFrame[keycode];

		//ASSERT(false);
		return false;
	}

	Touches Input::getTouches()
	{
		return *_touches;
	}

	vec2f Input::getTouchPosition(unsigned int touchId)
	{
		if (touchId < maxTouches)
			return _touches->touch[touchId].position;

		//ASSERT(false);  // TODO: Logging
		return { -1, -1 };
	}

	// j�nky android mouse
	void Input::setMouseState(bool down, vec2f location)
	{
		*_mousePos = location;
		_mouseButtons[1] = down;
	}



	Touch Input::getTouch(unsigned int touchId)
	{
		if (touchId < maxTouches)
			return _touches->touch[touchId];

		//ASSERT(false);
		return {}; // TODO: ...
	}

