#ifndef MAPGRID_H
#define MAPGRID_H

// buildingit gridiin vs 100 x 100 = 10000 x 1   vs   10000 x 4  
// laita map kun haluat hyvaa
// 

constexpr float TILE_SIZE = 20.f;
constexpr int MAP_W = 100;
constexpr int MAP_H = 100;

struct Map
{
	bool blocked[MAP_W * MAP_H];
	int  buildingId[MAP_W * MAP_H];
};

enum class TileType
{
	Empty,
	grass,
	Max
};

static void initMap(Map* map, int width, int height);

static int getBuildingId(Map* map, int x, int y);
static bool blocked(Map* map, int x, int y);

static vec2i worldToGrid(vec2f& position);
static bool tilesBlocked(Map* map, int x, int y, const vec2i& size);
static void blockTiles(Map* map, int x, int y, const vec2i& size);

#endif // end of the header


#ifdef MAPGRID_IMPL
static void initMap(Map* map, int width, int height)
{
	*map = {};

	for (int i = 0; i < height; ++i)
	{
		for (int j = 0; j < width; ++j)
		{
			// ...
		}
	}
}

static int getBuildingId(Map* map, int x, int y)
{
	return { map->buildingId[x + y * MAP_W] };
}

static bool blocked(Map* map, int x, int y)
{
	return map->blocked[x + y * MAP_W];
}

static vec2i worldToGrid(vec2f& position)
{
	return { int(position.x / TILE_SIZE), int(position.y / TILE_SIZE) };
}

static bool tilesBlocked(Map* map, int x, int y, const vec2i& size)
{
	for (int j = y; j < y + size.y; ++j)
	{
		for (int i = x; i < x + size.x; ++i)
		{
			if (map->blocked[j * MAP_W + i])
			{
				return true;
			}
		}
	}
	return false;
}

static void blockTiles(Map* map, int x, int y, const vec2i& size)
{
	for (int j = y; j < y + size.y; ++j)
	{
		for (int i = x; i < x + size.x; ++i)
		{
			map->blocked[j * MAP_W + i] = true;
		}
	}
}

static void setBuilding(Map* map, int x, int y, const vec2i& size, int entityId)
{
	for (int j = y; j < y + size.y; ++j)
	{
		for (int i = x; i < x + size.x; ++i)
		{
			map->buildingId[j * MAP_W + i] = entityId;
		}
	}
}

#define NO_BUILDING -1
static void destroyBuilding(Map* map, int x, int y, const vec2i& size)
{
	for (int j = y; j < y + size.y; ++j)
	{
		for (int i = x; i < x + size.x; ++i)
		{
			map->buildingId[j * MAP_W + i] = NO_BUILDING;
		}
	}
}

void debug_RenderCollisionGrid(EngineCore* core, Map* map, vec4* cameraRect, Layer* layer)
{
	// gameState
	// draw solid square here!
	vec2f xy = { cameraRect->x, cameraRect->y };
	vec2f wh = { xy.x + cameraRect->w, xy.y + cameraRect->h };
	vec2i start = worldToGrid(xy);
	vec2i end = worldToGrid(wh);

	layer->setTexture(engine::getTexture(core->resources, textures::Texture_Block)->ID);

	static vec4 uv{ 0.f, 0.f, 1.0f, 1.0f };
	vec4 color = vec4{ 247.0f / 255.f, 22.f / 255.f, 22.f / 255.f, 0.4f };
	for (int i = start.x; i < end.x; ++i)
	{
		for (int j = start.y; j < end.y; ++j)
		{
			vec4 pos{ j * TILE_SIZE, i * TILE_SIZE, TILE_SIZE, TILE_SIZE };

			if (map->blocked[i * MAP_W + j])
			{
				layer->draw(&pos, &uv, &color);
			}
		}
	}
}
#endif
