enum class Animations
{
	Attack_villager,
	Run_villager,
};

struct Animation
{
	int count;
	vec4* uvs;
};

struct SpriteSheet
{
	int count;
	int texId;
	vec4* idToUvs;
};

struct Anim
{
	float nextChange;
	char  nextId;
	SpriteSheet* sheet;
};

Animation animations[10];
void updateAnims(Anim* anims)
{
	float currentTime = 0.f;
	if (anims->nextChange < currentTime)
	{
		if (++anims->nextId == anims->sheet->count)
		{
			anims->nextId = 0;
		}
		// newuv = anims->sheet[anims->nextId];
	}
}


// constexpr float SIZE_X = 12;
// constexpr float SIZE_Y = 4;

#define TileX(index) (int)((index) % (int)SIZE_X)
#define TileY(index) (int)((index) / SIZE_X)

#define getFrameUvsm(index) { TileX(index) / SIZE_X, 1.0f - ((TileY(index) + 1.f) / (float)SIZE_Y), 1.0f / SIZE_X, 1.0f / SIZE_Y }

#define getFrameUv(indexX, indexY, textureW, textureH, offsetX, offsetY, perOneWidth, perOneHeight) \
	{ (((offsetX) + ((indexX) * (perOneWidth)))    / (float)(textureW)), \
	   1.0f - ((offsetY + (perOneHeight) * (indexY)) / (float)(textureH)), \
	  ((perOneWidth) / (textureW)), \
	  ((perOneHeight) / (textureH)) \
	}

#define getFrameUvWithPadding(indexX, indexY, textureW, textureH, offsetX, offsetY, perOneWidth, perOneHeight, padding) \
	{ ( (((int)padding * (int)indexX) + (offsetX) + ((indexX) * (perOneWidth)))       / (float)(textureW)), \
	   1.0f - ((float)(((int)padding * (int)indexY) + (offsetY) + (perOneHeight) * (indexY)) / (float)(textureH)), \
	  ((perOneWidth) / (textureW)), \
	  ((perOneHeight) / (textureH)) \
	}

SpriteSheet getIdToUvArray(engine::Texture2D * texture, int perWidth, int perHeight, int perOneW, int perOneH, int offsetX, int offsetY, int padding)
{
	SpriteSheet result;
	result.texId = texture->ID;

	int w = texture->width;
	int h = texture->height;
	int x = texture->width / perOneW;
	int y = texture->height / perOneH;

	const int size = x * y;
	result.idToUvs = new vec4[size];

	for (int i = 0; i < size; i++)
	{
		int x = i % perWidth;
		int y = (i - x) / perWidth;
		result.idToUvs[i] = getFrameUvWithPadding((float)x, (float)y, (float)w, (float)h,
			(float)offsetX, (float)offsetY, (float)perOneW, (float)perOneH, (float)padding);
	}
	return result;
}

SpriteSheet spriteSheets[10];
void testInitAnimations(EngineCore* core)
{
	auto textAlien = engine::getTexture(core->resources, textures::Texture_Alien);
	spriteSheets[0] = getIdToUvArray(textAlien, 4, 5, textAlien->width / 4, textAlien->height / 5, 0, 0, 0);
}

