	void LineRenderer::Init()
	{
		glGenBuffers(1, &vbo);

#ifndef __EMSCRIPTEN__
		glGenVertexArrays(1, &vao);
#else
		glGenVertexArraysOES(1, &vao);
#endif
		glBindVertexArray(vao);

		glBindBuffer(GL_ARRAY_BUFFER, vbo);
		glEnableVertexAttribArray(0);
		glVertexAttribPointer(0, 2, GL_FLOAT, GL_FALSE, 0, 0);


#ifndef __EMSCRIPTEN__
		glGenVertexArrays(1, &pointVao);
#else
		glGenVertexArraysOES(1, &pointVao);
#endif
		glBindVertexArray(pointVao);

		glBindBuffer(GL_ARRAY_BUFFER, vbo);
		glEnableVertexAttribArray(0);
		glVertexAttribPointer(0, 2, GL_FLOAT, GL_FALSE, 0, 0);


#if 0
		glGenVertexArrays(1, &circleVao);
		glGenBuffers(1, &circleVbo);
		glGenBuffers(1, &realCircleVbo);

		glBindVertexArray(circleVao);
		glBindBuffer(GL_ARRAY_BUFFER, circleVbo);

		vec2f points[200];
		{
#define PI 3.141592f
			const int NUM_VERTS = 100;
			const int radius = 20.f;
			const vec2f center{ 0.f, 0.f };
			float x = cosf(0) * radius + center.x;
			float y = sinf(0) * radius + center.y;
			Vec2 last{ x, y };
			for (int i = 0, j = 0; i < NUM_VERTS; i++, j += 2)
			{
				float angle = ((float)i / NUM_VERTS) * PI * 2.0f;
				float x = cos(angle) * radius + center.x;
				float y = sin(angle) * radius + center.y;
				Vec2 pos{ x, y };
				points[j] = pos;
				points[j + 1] = last;
				// da_push(*lines, pos);
				// da_push(*lines, last);
				last.x = x;
				last.y = y;
				// m_verts[start + i].color = color;
			}
			Vec2 pos = { x, y };
			// points[]
		}

		glBindBuffer(GL_ARRAY_BUFFER, realCircleVbo);
		glBufferData(GL_ARRAY_BUFFER, 200, points, GL_STATIC_DRAW);
		glEnableVertexAttribArray(0);
		glVertexAttribPointer(0, 2, GL_FLOAT, GL_FALSE, sizeof(GLfloat) * 2, (void*)(0));

		glBindBuffer(GL_ARRAY_BUFFER, circleVbo);
		glEnableVertexAttribArray(1);
		glVertexAttribPointer(1, 2, GL_FLOAT, GL_FALSE, sizeof(GLfloat) * 2, (void*)(0));
		glVertexAttribDivisor(1, 1);
#endif

	}

	void DrawGeometry(Geometry mode, engine::Shader* shader, Vertex* vertices,
		Vertex* indices, int count)
	{
		switch (mode) {
		case Geometry::Line:
		{
#if 1
			glBindBuffer(GL_ARRAY_BUFFER, vbo);
			glBufferData(GL_ARRAY_BUFFER, sizeof(Vertex) * count, vertices, GL_DYNAMIC_DRAW);

			glBindVertexArray(vao);
			glDrawArrays(GL_LINES, 0, count);
#else
			glVertexAttribPointer(0, 2, GL_FLOAT, GL_FALSE, 0, vertices->data());
			glDrawArrays(GL_LINES, 0, count);
#endif
		} break;
		case Geometry::Point:
		{
			glBindBuffer(GL_ARRAY_BUFFER, vbo);
			glBufferData(GL_ARRAY_BUFFER, sizeof(Vertex) * count, vertices, GL_DYNAMIC_DRAW);

			// glVertexAttribPointer(0, 2, GL_FLOAT, GL_FALSE, 0, 0);
			// glEnableVertexAttribArray(0);
			glBindVertexArray(pointVao);

			glDrawArrays(GL_POINTS, 0, count);
			glBindBuffer(GL_ARRAY_BUFFER, 0);
		} break;
		case Geometry::Triangles: break;
		case Geometry::Quads:     break;
		default:;
		}
	}

