#include "Entity.h"

constexpr int DEAD_UNIT = -1;

EntityHandle* newHandle(Entities* entities, EntityHandles* entityHandles, int entityId)
{
	++entityHandles->currentCount;
	int freeIndex = entityHandles->firstFreeIndex;
	EntityHandle* result = &entityHandles->handles[freeIndex];
	result->id = entityId;
	result->_index = freeIndex;
	result->refCount = 1;

	entities->selfHandle[entityId] = result;
		
	// find next free
	for (int i = entityHandles->firstFreeIndex + 1; i < MAX_ENTITY_COUNT; ++i)
	{
		if (entityHandles->handles[i].refCount == 0)
		{
			entityHandles->firstFreeIndex = i;
			break;
		}
	}

	return result;
}

EntityHandle* getHandle(Entities* entities, int entityId)
{

	if (!entities->selfHandle[entityId])
	{
		return newHandle(entities, &entityHandles, entityId);
	}
	else
	{
		++entities->selfHandle[entityId]->refCount;
		return entities->selfHandle[entityId];
	}
}

void invalidateHandle(EntityHandle* handle)
{
	handle->id = DEAD_UNIT;
}

void freeHandle(Entities* entities, EntityHandle* entityHandle)
{
	--entityHandle->refCount;
	ASSERT(entityHandle->refCount >= 0); // -1 == baaaaaaaaaaad

	if (entityHandle->refCount == 0)
	{
		entities->selfHandle[entityHandle->id] = 0;

		if (entityHandles.firstFreeIndex > entityHandle->_index)
		{
			entityHandles.firstFreeIndex = entityHandle->_index;
		}
		entityHandles.handles[entityHandle->_index] = {}; //notice
	}
}
