static int frame = 0;
struct TickTimer {
	int nextTick, tickDelay;

	void init(int tickDelay)
	{
		this->tickDelay = tickDelay;
		this->nextTick = frame;
	}

	bool update()
	{
		if (frame >= nextTick)
		{
			nextTick = frame + tickDelay;
			return true;
		}
		return false;
	}
};

