int createEntity(Entities* entities, Vec2* position, Type type = Type_unit, Initializer* init = 0)
{
	int newID = entities->current_count;

	if (newID >= MAX_ENTITY_COUNT)
	{
		LOGI("WARNING entity max count reached not creating!");
		return -1;
	}

	++entities->current_count;

	switch (type)
	{
	case Type_building: // L O L
	{
		entities->buildingType[newID] = init->buildingType;
		
		entities->positions[newID] = { position->x, position->y, 20.f, 20.f }; // data
		entities->positions[newID].w = 60.f;
		entities->positions[newID].h = 60.f;
		entities->speed[newID] = 0.f;
		goto skip;
	} // fall
	case Type_unit:
	{
		if (init)
			entities->unitType[newID] = init->unitType;
		entities->positions[newID] = { position->x, position->y, 20.f, 20.f };
		entities->speed[newID] = UNIT_DEFAULT_SPEED;
		skip:

		entities->moveToPosition[newID] = { position->x, position->y };

		entities->colors[newID] = { 1.0f, 1.0f, 1.0f, 1.0f }; //	{ 255, 255, 255, 255 };
		entities->textureId[newID] = 1;
		entities->uvs[newID] = { 0.f, 0.f, 1.0f, 1.0f };

		entities->health[newID]    = 100;
		entities->baseRangeSqrt[newID] = 60.f * 60.f;
		entities->baseDamage[newID] = 10;
	} break;
	default:
		break;
	}

	entities->type[newID] = type;

	return newID;
}

void removeEntity(Entities* entities, int removedId)
{
	int lastEntity = entities->current_count - 1;

	// HANDLE is invalid

	// entities->selfHandle[removedId];
	if (EntityHandle* handle = getHandle(entities, removedId))
	{
		invalidateHandle(handle); // pelkastaan vihollisilla handleja
		// freeHandle(entities, handle);
	}


	entities->positions[removedId]      = entities->positions[lastEntity];
	entities->vel[removedId] = entities->vel[lastEntity];
	entities->speed[removedId]          = entities->speed[lastEntity];

	entities->uvs[removedId] = entities->uvs[lastEntity];
	entities->colors[removedId] = entities->colors[lastEntity];
	entities->moveToPosition[removedId] = entities->moveToPosition[lastEntity];
	entities->arrived[removedId] = entities->arrived[lastEntity];

	entities->state[removedId] = entities->state[lastEntity];
	entities->side[removedId] = entities->side[lastEntity];
	entities->attackType[removedId] = entities->attackType[lastEntity];
	entities->baseDamage[removedId] = entities->baseDamage[lastEntity];
	entities->health[removedId] = entities->health[lastEntity];
	entities->baseRangeSqrt[removedId] = entities->baseRangeSqrt[lastEntity];

	entities->type[removedId] = entities->type[lastEntity];
	entities->selfHandle[removedId] = entities->selfHandle[lastEntity];
	entities->target[removedId] = entities->target[lastEntity];

	entities->collisionPairs[removedId] = entities->collisionPairs[lastEntity];
	entities->COLLISION_TYPES[removedId] = entities->COLLISION_TYPES[lastEntity];

	entities->steeringForce[removedId] = entities->steeringForce[lastEntity];
	entities->collisionTypes[removedId] = entities->collisionTypes[lastEntity];
	entities->collisionTypesCount[removedId] = entities->collisionTypesCount[lastEntity];
	entities->moving[removedId] = entities->moving[lastEntity];
	entities->textureId[removedId] = entities->textureId[lastEntity];

	--entities->current_count;
}
