void move(Entities* entities, int entity, Vec2* amount)
{
	entities->positions[entity].x += amount->x;
	entities->positions[entity].y += amount->y;
}

void setSize(Entities* entities, Vec2* size, int id)
{
	entities->positions[id].w = size->x;
	entities->positions[id].h = size->y;
}

void setPos(Entities* entities, Vec2* size, int id)
{
	entities->positions[id].x = size->x;
	entities->positions[id].y = size->y;
}

bool circleCollisionCheck(vec4* __restrict a, vec4* __restrict b)
{
	const float MIN_DISTANCE = a->w + b->w;

	Vec2 centerPosA = Vec2{ a->x, a->y } + Vec2{ a->w, a->w };
	Vec2 centerPosB = Vec2{ b->x, b->y } + Vec2{ b->w, b->w };
	Vec2 distVec = centerPosA - centerPosB;

	const float distance = distVec.length();
	const float collisionDepth = MIN_DISTANCE - distance;

	return (collisionDepth > 0);
}

