/*__declspec(noalias)*/ 
static void phase1(Entities* gameState)
{
	Vec2 distVecs[MAX_ENTITY_COUNT];

	float distances[MAX_ENTITY_COUNT];
	// bool haha[MAX_ENTITY_COUNT];

// #pragma omp parallel for
// #pragma omp parallel for ordered schedule(dynamic)
	for (int i = 0; i < gameState->current_count; i++)
	{
		vec4* __restrict a = &gameState->positions[i];

		for (int j = i + 1; j < gameState->current_count; j++)
		{
			vec4* __restrict b = &gameState->positions[j];

			// const float MIN_DISTANCE = a->w + b->w;  // molempiend dist
			const float MIN_DISTANCE = a->w / 2.f + b->w / 2.f;  // molempiend dist
			Vec2 centerPosA = Vec2{ a->x, a->y } +Vec2{ a->w / 2.f , a->w / 2.f };
			Vec2 centerPosB = Vec2{ b->x, b->y } +Vec2{ b->w / 2.f , b->w / 2.f };

			// distVecs[i] = distVecs[i].x * distVecs[i].x + distVecs[i].y * distVecs[i].y;
			distVecs[j] = centerPosA - centerPosB;

			//		haha[j] = std::abs(distVecs[j].x * distVecs[j].x + distVecs[j].y * distVecs[j].y) 
			//			< MIN_DISTANCE * MIN_DISTANCE ? 1 : 0;

			distances[j] = MIN_DISTANCE * MIN_DISTANCE;
#if 1

#endif
			// std::abs(distVecs[j].x * distVecs[j].x + distVecs[j].y * distVecs[j].y) 
		}
		// std::abs(distVecs[j].x * distVecs[j].x + distVecs[j].y
		for (int j = i + 1; j < gameState->current_count; j++)
		{
			float value = std::abs(distVecs[j].x * distVecs[j].x + distVecs[j].y * distVecs[j].y);
			if (std::abs(value) < distances[j])
			{
				// distVecs[j].normalizeInPlace();
				float sqrtd = sqrt(value);
				distVecs[j].x /= sqrtd;
				distVecs[j].y /= sqrtd;

				distVecs[j] *= (/*distances[j]*/ 20.f * 0.5f * 0.15f);
				// distVecs[j] *= (/*distances[j]*/ 20.f  0.5f * 0.15f);

				// vec4* a = &gameState->positions[j];
				vec4* __restrict b = &gameState->positions[j];

				const Vec2 aResolution = distVecs[j]; /// 1.5f; // +=
				a->x += aResolution.x;
				a->y += aResolution.y;
				const Vec2 bResolution = distVecs[j]; /// 1.5f;  // -=
				b->x -= bResolution.x;
				b->y -= bResolution.y;
			}
		}
	}

	// laita yksi for luupin sis� looppi

//	for (int i = 0; i < gameState->current_count; i++)
	{

	}
}









