void debug_RenderSteeringForce(EngineCore* core, Entities* entities)
{
	int count = entities->current_count;
	vec4 cameraRect = camera::cameraRect(core);
	for (int i = 0; i < count; i++)
	{
		vec4* epos = &entities->positions[i];
		vec2f pos = { epos->x + epos->w / 2.f, epos->y + epos->h / 2.f };
		vec2f endPoint = ((entities->steeringForce[i] * 2.5) + pos);

		if (camera::cull(&cameraRect, pos))
		{
			graphics::drawLine(core->lines, &pos, &endPoint);
		}
	}
}

void drawEntities(Entities* entities, EngineCore* core)
{
	// LOGI("olen fine : %i \n", entities->current_count);
	for (int i = 0; i < entities->current_count; i++)
	{
		// LOGI("I: %i \n", i);
		auto* position = &entities->positions[i];
		auto* uv = &entities->uvs[i];
		auto* color = &entities->colors[i];

		core->layers[0].draw(position, uv, color);
	}
	// LOGI("OLEN FINE\n");

#if _WIN32
	ImGui::Checkbox("phase ", &phase);
#endif
	// LOGI("OLEN FINE\n");
}
