static void initAoe160k(EngineCore* core, RtsGameState* gameState)
{
	LOGI("Welcome to the aoe 160k\n");

#ifdef __EMSCRIPTEN__
	gameState->entities = { };
#endif

	// Entityt
	Vec2 pos{ 137.f, 137.f };
	gameState->player = createEntity(&gameState->entities, &pos);

	pos.y = 80.f;
	float baseY = 500.f;
	float baseX = 500.f;

	int lineCount = 10;
	for (int j = 0; j < lineCount; ++j)
	{
		for (int i = 0; i < benchmarkCount; ++i)
		{
			pos.x = baseX + (float)i * 2.f;
			pos.y = float(sin(i) * 80.f + baseY);
			createEntity(&gameState->entities, &pos);
		}
		baseY += 500.f;
	}

	// LOGI("ENTITY COUNT %i \n", gameState->entities.curre)
}

constexpr float dampener = 0.99f;
void updateFloaters(FloatingText* floaters, int count)
{
	for (int i = 0; i < count; i++)
	{
		floaters[i].vel += floaters[i].acc;
		floaters[i].acc *= dampener;
		floaters[i].position += floaters[i].vel;
	}
}

// unit selection rect
static inline vec4 getSelectionRect(RtsGameState* gameState, EngineCore* core)
{
	auto mouseStart = gameState->rtsController.onClickPosition;
	vec2f mouseEnd = engine::Input::getMousePositionWorld(core);

	vec2f mouseWidth = mouseEnd - mouseStart;
	return { mouseStart.x, mouseStart.y, mouseWidth.x, mouseWidth.y };
}

static inline vec4 makeSelectionRect(vec4 rect)
{
	if (rect.w < 0.f)
	{
		rect.w = -rect.w;
		rect.x -= rect.w;
	}

	if (rect.h < 0.f)
	{
		rect.h = -rect.h;
		rect.y -= rect.h;
	}

	return rect;
}



static void selectUnits(RtsGameState* gameState, EngineCore* core)
{
	RtsController* controller = &gameState->rtsController;

	int count = gameState->entities.current_count;
	int selectedUnits = 0;
	vec4 selectionRect = makeSelectionRect(getSelectionRect(gameState, core));

	for (int i = 0; i < count; ++i)
	{
		vec4* position = &gameState->entities.positions[i];

		if (maths::isInsideRect(&selectionRect, position))
		{
			controller->selectedUnits[selectedUnits] = i;
			++selectedUnits;
		}
	}

	bool onlyBuildings = true;
	Entities* entities = &gameState->entities;
	for (int i = 0; i < selectedUnits; ++i)
	{
		int entityId = controller->selectedUnits[i];
		if (entities->type[entityId] == Type_unit)
		{
			onlyBuildings = false;
			break;
		}
	}

	if (onlyBuildings)
	{
		controller->selectionType = Type_building;
	}
	else
	{
		controller->selectionType = Type_unit;
		for (int i = 0; i < selectedUnits; ++i)
		{
			int entityId = controller->selectedUnits[i];
			if (entities->type[entityId] != Type_unit)
			{
				// remove building from selected
				entities->type[entityId] = entities->type[selectedUnits - 1];
				--selectedUnits;
			}
		}
	}

	controller->selectedUnitCount = selectedUnits;
	LOGI("Selected %i units\n", selectedUnits);
}

static void setMovePosition(RtsGameState* gameState, int* moving, int count, vec2f& moveTo)
{
	for (int i = 0; i < count; ++i)
	{
		int entityId = *(moving + i);
		vec2f* position = &gameState->entities.moveToPosition[entityId];

		*position = moveTo;
		gameState->entities.moving[entityId] = true;
	}
}

static void setGroupMovePosition(RtsGameState* gameState, int* moving, int count, vec2f& moveTo)
{
	Entities* ents = &gameState->entities;
	// moving [0] == ekan id
	vec2f centerPoint = { ents->positions[moving[0]].x, ents->positions[moving[0]].y };

	for (int i = 1; i < count; ++i)
	{
		int entityId = moving[i];
		centerPoint += { ents->positions[entityId].x, ents->positions[entityId].y };
	}
	centerPoint /= (float)count; // kaikkien keskus paikka


	// set up debug info !
	Formation* formation = &gameState->PlayerLastFormation;
	formation->formationUnitsCenterPosition = centerPoint; // +moveTo;
	formation->formationUnitsMoveToCenterPosition = moveTo;
	formation->formationCount = count;
	formation->formationRadius = sqrtf((float)count) * 12.5f;

	float formationRadiusSquared = formation->formationRadius * formation->formationRadius;

	vec2f center = moveTo + centerPoint;

	float centeredCount = (float)count;
	float maxCenterCount = (float)count;
	float centerMax = (float)count;
	float fromCenter = 15.f;


	for (int i = 0; i < count; ++i)
	{
		int entityId = moving[i];
		vec2f startingPoint = vec2f{ ents->positions[entityId].x, ents->positions[entityId].y } -centerPoint;

		float distanceSquared = abs(startingPoint.x * startingPoint.x) + abs(startingPoint.y * startingPoint.y);

		if (formationRadiusSquared - distanceSquared < 0.f)
		{
#if 0
			vec2f direction = moveTo - startingPoint; // moveTo / center
			direction.normalizeInPlace();
#else
			vec2f direction = startingPoint.normalize();
#endif

			float centerCoeff = centeredCount / maxCenterCount;

			// float dist = sqrt(std::abs(formationRadiusSquared - distanceSquared));
			// float howFarCoeff = distanceSquared / formationRadiusSquared; 
			direction *= formation->formationRadius * centerCoeff; // dist;
			ents->moveToPosition[entityId] = (direction)+moveTo;

			// LOGI("mene keskustaan\n");
			centeredCount -= fromCenter;
			if (centeredCount < 0.f || centeredCount > centerMax)
			{
				centeredCount = (float)count; // MAN THE CENTERS
				centeredCount = 0.f;
				fromCenter = -3.f;     // aletaan menn� keskustasta reunoihin
			}
		}
		else
		{
			ents->moveToPosition[entityId] = startingPoint + moveTo;
		}
		ents->moving[entityId] = true;
	}
}

