constexpr float arriveRange = 10.0f * 10.0f;
static void calculateVelocities(RtsGameState* gameState)
{
	auto* ents = &gameState->entities;
	int count = getCount(gameState);

	for (int i = 0; i < count; i++)
	{

		// if (ents->COLLISION_TYPES[i] != collided_hard)
		{
			if (ents->moving[i])
			{
				vec4* curPos = &ents->positions[i];
				vec2f pos = { curPos->x, curPos->y };
				vec2f direction = ents->moveToPosition[i] - pos;

				float squaredDistance = direction.x * direction.x + direction.y * direction.y;

				if (squaredDistance > arriveRange)
				{
#if NORMAL
					direction.normalizeInPlace();
					ents->vel[i] = direction * ents->speed[i];
#else
					direction.normalizeInPlace();
					ents->vel[i] = (direction * 1.75) + ents->steeringForce[i];
					ents->vel[i].normalizeInPlace();
					ents->vel[i] *= ents->speed[i];
#endif
				}
				else
				{
					ents->vel[i] = vec2f{ 0.f, 0.f };
					ents->moving[i] = false;
				}
			}
			else // get pushed
			{
				ents->vel[i] = ents->steeringForce[i];
				ents->vel[i].safeNormalizeInPlace();
				// ents->vel[i] *= 2.f;
				ents->steeringForce[i].x = 0.f;
				ents->steeringForce[i].y = 0.f;
			}
		}
	}
}


