void buildSteeringVelocity(RtsGameState* gameState)
{
	Entities* ents = &gameState->entities;
	int count = ents->current_count;


	for (int i = 0; i < count; ++i)
	{
		vec2f forceVector{ 0.f, 0.f };
		int collisionCount = da_size(ents->collisionPairs[i]);//ents->collisionTypesCount[i];
		CollisionPair* pairs = ents->collisionPairs[i];
		// vec2f pos0{ ents->positions[i].x, ents->positions[i].y };

		for (int j = 0; j < collisionCount; ++j)
		{
			// float forceMagnitude; 
			// vec2f pos1{ ents->positions[i].x, ents->positions[i].y };
			// vec2f directionVec = pos0 - pos1;
			// directionVec.normalizeInPlace(); // gets calculated twice

			/*switch (ents->collisionTypes[i][j].type)
			{
			case collision_hard:
				forceMagnitude = 2;
				break;
			case collision_soft:
				forceMagnitude = 1;
				break;
			case collision_attraction:
				forceMagnitude = -0.25;
				break;
			}*/

			forceVector += (pairs[j].direction * pairs[j].magnitude);
		}

		ents->steeringForce[i] = forceVector;
		// ents->vel[i] = forceVector;
		da_clear(ents->collisionPairs[i]);
	}
}

