#include <Camera2D.h>
#include <cmath>

namespace engine {

	Camera2D::Camera2D() :
		_needsMatrixUpdate(true),
		_position(0.0f, 0.0f),
		_cameraMatrix(1.0f),
		_orthoMatrix(1.0f),
		_scale(1.0f),
		_screenWidth(500),
		_screenHeight(500),
		angle(0), yaw(0), pitch(0)
	{
	}


	Camera2D::~Camera2D()
	{
	}

	void Camera2D::init(int screenWidth, int screenHeight)
	{
		_screenWidth = screenWidth;
		_screenHeight = screenHeight;
		_orthoMatrix = glm::ortho(0.0f, (float)_screenWidth, 0.0f, (float)_screenHeight);
	}

	void Camera2D::update()
	{
		if (_needsMatrixUpdate)
		{
			//glm::vec3 translate(-_position.x, -_position.y, 0.0f);
			//negatiivinen position,että kun menemme ite vasemmalle kaikki muu menee oikealle  kolmas olisi kolmiulotteiselle
			//translate liikuttaa objecteja ruudussa 

			glm::vec3 translate(-_position.x + _screenWidth / 2, -_position.y + _screenHeight / 2, 0.0f);  //centeroi cameran myösä
			_cameraMatrix = glm::translate(_orthoMatrix, translate);


			glm::mat4 matRoll = glm::mat4(1.0f);
			glm::mat4 matPitch = glm::mat4(1.0f);
			glm::mat4 matYaw = glm::mat4(1.0f);

			matRoll = glm::rotate(matRoll, angle, glm::vec3(0.f, 0.f, 1.0f));
			matPitch = glm::rotate(matPitch, pitch, glm::vec3(1.0f, 0.0f, 0.0f));
			matYaw = glm::rotate(matYaw, yaw, glm::vec3(0.0f, 1.0f, 0.0f));

			glm::mat4 rotate = matRoll * matPitch * matYaw;

			// kun yksi niin normaali eli kerrotaan yhdellä | jos 0.5 zoomattu ulos ja 2.o niiin zoom in
			glm::vec3 scale(_scale, _scale, 0.0f);
			_cameraMatrix = rotate * glm::scale(glm::mat4(1.0f), scale) * _cameraMatrix;
			//camera scale^^

			_needsMatrixUpdate = false;
		}
	}

	void Camera2D::zoom(float factor)
	{
		_scale = _scale + factor;
		_needsMatrixUpdate = true;
		if (_scale < 0.01f)
			_scale = 0.f;
	}

	glm::vec2 Camera2D::convertScreenToWorld(glm::vec2 screenCoords)
	{
		//invert y direction
		screenCoords.y = _screenHeight - screenCoords.y;
		// tehdään keskustasta 0,0
		screenCoords -= glm::vec2(_screenWidth / 2, _screenHeight / 2);
		//scalettaa maailman koordinaatiti
		screenCoords /= _scale;
		//translate with the camera position
		screenCoords += _position;
		return screenCoords;
	}
	//AABB TEST to see if the box is in the camera view
	bool Camera2D::isBoxinView(const glm::vec2 &position, const glm::vec2 &dimensions)
	{
		glm::vec2 scaledScreenDimensions = glm::vec2(_screenWidth, _screenHeight) / _scale;

		const float MIN_DISTANCE_X = dimensions.x / 2.0f + scaledScreenDimensions.x / 2.0f;
		const float MIN_DISTANCE_Y = dimensions.y / 2.0f + scaledScreenDimensions.y / 2.0f;


		glm::vec2 centerPos = position + dimensions / 2.0f;

		glm::vec2 centerCameraPos = _position;		//center position of the camera

		glm::vec2 distVec = centerPos - centerCameraPos;			//vector from the input to the camera

		float xDepth = MIN_DISTANCE_X - fabs(distVec.x);
		float yDepth = MIN_DISTANCE_Y - fabs(distVec.y);

		if (xDepth > 0 && yDepth > 0)
		{

			// there was a collision 
			return true;

		}
		return false;
	}

	vec4 Camera2D::getCameraRect()
	{
		auto bot = convertScreenToWorld({ _screenWidth, _screenHeight });
		auto top = convertScreenToWorld({ 0, 0 });

		float x0, y0, x1, y1;
		x0 = top.x;
		y0 = bot.y;
		x1 = bot.x;
		y1 = top.y;

		return { x0, y0, x1, y1 };
	}
}