#define ipos 0
#define iuv  1
#define iid  2
#define icolor 3
struct RenderData
{
	float* buffer[3];
	float* base[3];

	unsigned int* baseColor, *bufferColor;

	float ts = 0.f;
	int indCount = 0;

	int MAX_TEXTURES = 8;
	unsigned int textureSlots[32];
	unsigned int currentSlots = 0;

	int maxSize = 0;

	void initRenderData(int maxSize)
	{
		buffer[ipos] = base[ipos] = new float[maxSize * 3 * 4]; // 3 per 4
		buffer[iuv] = base[iuv] = new float[maxSize * 2 * 4];
		buffer[iid] = base[iid] = new float[maxSize * 1 * 4];
		baseColor = bufferColor = new unsigned int[maxSize * 1 * 4];
	}

	void setTexture(int texId)
	{
		bool found = false;
		int i = 0;
		float ts = 0.0f;
		for (; i < MAX_TEXTURES; i++)
		{
			if (texId == textureSlots[i])
			{
				ts = (float)(i + 1);
				found = true;
				break;
			}
		}

		if (!found)  // huom loop ^^
		{
			textureSlots[currentSlots] = texId;
			++currentSlots;
			ts = (float)(currentSlots);
		}

		this->ts = ts;
	}

	void DrawSprite(vec4* __restrict posSize, vec4* __restrict uv, vec4* __restrict color)
	{
		// if (indSize == maxIndicesSize)
			// *((int*)0) = 12;
			//ASSERT(false);
		int r = int(color->x * 255.0f);
		int g = int(color->y * 255.0f);
		int b = int(color->w * 255.0f);
		int a = int(color->h * 255.0f);

		unsigned int c = (a << 24 | b << 16 | g << 8 | r);


		*(bufferColor + 0) = c;
		*(bufferColor + 1) = c;
		*(bufferColor + 2) = c;
		*(bufferColor + 3) = c;
		bufferColor += 4;


		*(buffer[ipos] + 0) = posSize->x;
		*(buffer[ipos] + 1) = posSize->y + posSize->h;
		*(buffer[ipos] + 2) = 0.0f;

		*(buffer[ipos] + 3) = posSize->x;
		*(buffer[ipos] + 4) = posSize->y;
		*(buffer[ipos] + 5) = 0.0f;

		*(buffer[ipos] + 6) = posSize->x + posSize->w;
		*(buffer[ipos] + 7) = posSize->y;
		*(buffer[ipos] + 8) = 0.0f;

		*(buffer[ipos] + 9) = posSize->x + posSize->w;
		*(buffer[ipos] + 10) = posSize->y + posSize->h;
		*(buffer[ipos] + 11) = 0.0f;

		buffer[ipos] += 12;

		*(buffer[iuv] + 0) = uv->x;
		*(buffer[iuv] + 1) = uv->y;

		*(buffer[iuv] + 2) = uv->x;
		*(buffer[iuv] + 3) = uv->y + uv->h;

		*(buffer[iuv] + 4) = uv->x + uv->w;
		*(buffer[iuv] + 5) = uv->y + uv->h;

		*(buffer[iuv] + 6) = uv->x + uv->w;
		*(buffer[iuv] + 7) = uv->y;

		buffer[iuv] += 8;

		*(buffer[iid] + 0) = ts;
		*(buffer[iid] + 1) = ts;
		*(buffer[iid] + 2) = ts;
		*(buffer[iid] + 3) = ts;

		buffer[iid] += 4;
		indCount += 6;
	}

};

/* initialize state to random bits */
static unsigned long state[16];
static unsigned int r_index = 0;
/* init should also reset this to 0 */
/* return 32 bit random number */

#define RNG_MAX 0xffffffff
static unsigned long WELLRNG512(void)
{
	unsigned long a, b, c, d;
	a = state[r_index];
	c = state[(r_index + 13) & 15];
	b = a^c ^ (a << 16) ^ (c << 15);
	c = state[(r_index + 9) & 15];
	c ^= (c >> 11);
	a = state[r_index] = b^c;
	d = a ^ ((a << 5) & 0xDA442D24UL);
	r_index = (r_index + 15) & 15;
	a = state[r_index];
	state[r_index] = a^b^d ^ (a << 2) ^ (b << 18) ^ (c << 28);
	return state[r_index];
}

struct Benchmark { float* positions; float* velocities; };

static float RandomFloat(float a, float b) {
	float random = ((float)WELLRNG512()) / (float)RNG_MAX;
	float diff = b - a;
	float r = random * diff;
	return a + r;
}


