struct Layer
{
	// engine::shader _shader;
	unsigned int shader;
	int order;
	bool active;
	// const char* layerName;
	RenderData* data;
	// array of sprites or smt

	FontData* fontCommands = 0;

	char* strBuffer = 0;
	int bufferLen = 0;
	int currentLen = 0;

	void draw(vec4* __restrict posSize, vec4* __restrict uv, vec4* __restrict color, int textureId)
	{
		data->setTexture(textureId);
		data->DrawSprite(posSize, uv, color);
	}
	// automatic caching ^^

	void setTexture(int id) {
		data->setTexture(id);
	}

	void draw(vec4* __restrict posSize, vec4* __restrict uv, vec4* __restrict color)
	{
		data->DrawSprite(posSize, uv, color);
	}

#define growLen 4
#define initialBuffer 32
	// watch out for allocating from dll!
	void drawString(const char* str, Vec2* position, unsigned int color, int fontID) // smt
	{
		int len = (int)strlen(str);
		if (len + currentLen > bufferLen)
		{
			if (!strBuffer) {
				strBuffer = (char*)malloc(sizeof(char) * initialBuffer);
				bufferLen = initialBuffer;
				currentLen = 0;
			}
			else
			{
				char * buf = NULL;
				buf = (char*)realloc(strBuffer, growLen * bufferLen * sizeof(char));

				if (buf)
				{
					strBuffer = buf;
					bufferLen = growLen * bufferLen;
				}
			}
		}

		FontData e{ *position, color, fontID, len };
		da_push(fontCommands, e);

		memcpy(strBuffer + currentLen, str, len);
		currentLen += len;

	}
};

enum
{
	Order_first,
	Order_second,
	Order_third,
};

static Layer newLayer(int maxSize, int order)
{
	Layer newLayer{};
	newLayer.active = true;
	newLayer.order = order;
	newLayer.data = new RenderData();
	newLayer.data->initRenderData(maxSize);

	return newLayer;
}




