bool log(LogBuffer* log, const char* message, ...) // WHAT IS THE LOG LEN
{
	va_list argptr;

	va_start(argptr, message);
	// vfprintf(file, message, argptr);

	int n = vsprintf(log->buffer + log->count, message, argptr);
	va_end(argptr);

	log->count += n;
	// fclose(file);
	return true;
}

bool writeLogToFile(LogBuffer* log, int frame, const char* logFile)
{
	FILE* file;
	file = fopen(logFile, "a");

	if (!file)
	{
		fprintf(
			stderr,
			"ERROR: could not open LOG_FILE %s file for appending\n",
			LOG_FILE
		);
		return false;
	}
	else
	{
		char frameb[64];
		sprintf(frameb, "frame: %i", frame);
		fputs(frameb, file);
		fwrite(log->buffer, sizeof(char), log->count, file);
		fputc('\n', file);
		log->count = 0;
		fclose(file);
	}
	return true;
}

