// #include <Input.h>

struct Timer
{
	float next, tickTime;

	bool update(float currentTime) {
		if (currentTime > next) {
			next = tickTime + currentTime;
			return true;
		}
		return false;
	}
};

enum Shapes
{
	Shape_I,
	Shape_L,
	Shape_J,
	Shape_O,
	Shape_S,
	Shape_Z,
	Shape_T,
	Shape_Max,
};

#define BLOCK_WIDTH  26.f
#define BLOCK_HEIGHT 26.f
#define START_X 5
#define START_Y 20
#define NEXT_SHAPE_X 14
#define NEXT_SHAPE_Y 12

// 10 * 40  -> 
static int getRandomShapeId()
{
	// return rand() % Shape_Max;
	return WELLRNG512() % Shape_Max;
}

static ActiveBlocks getNextShape(Shapes shape, int startX, int startY)
{
	ActiveBlocks next;
	next.Color = BlockColors[0]; // <- get Color
	next.block = (Block)(shape + 1);

	switch (shape)
	{
	case Shape_I:
		next.positions[0] = { 0, 0 };
		next.positions[1] = { 0, 1 };
		next.positions[2] = { 0, 2 };
		next.positions[3] = { 0, 3 };
		break;
	case Shape_L:
		next.positions[0] = { 0, 0 };
		next.positions[1] = { 0, 2 };
		next.positions[2] = { 0, 1 }; // pivot
		next.positions[3] = { 1, 0 };
		break;
	case Shape_J:
		next.positions[0] = { 1, 2 };
		next.positions[1] = { 1, 0 };
		next.positions[2] = { 1, 1 }; // pivot
		next.positions[3] = { 0, 0 };
		break;
	case Shape_O:
		next.positions[0] = { 1, 0 };
		next.positions[1] = { 1, 1 };
		next.positions[2] = { 0, 0 }; // pivot
		next.positions[3] = { 0, 1 };
		break;
	case Shape_Z:
		next.positions[0] = { 0, 1 };
		next.positions[1] = { 1, 1 };
		next.positions[2] = { 1, 0 }; // pivot
		next.positions[3] = { 2, 0 };
		break;
	case Shape_S:
		next.positions[0] = { 2, 1 };
		next.positions[1] = { 1, 1 };

		next.positions[2] = { 1, 0 }; // pivot
		next.positions[3] = { 0, 0 };
		break;
	case Shape_T:
		next.positions[0] = { 1, 1 };
		next.positions[1] = { 2, 0 };
		next.positions[2] = { 1, 0 }; // pivot
		next.positions[3] = { 0, 0 };
		break;
	default:
		break;
	}

	for (int i = 0; i < 4; i++)
	{
		next.positions[i].x += startX;
		next.positions[i].y += startY;
	}

	return next;
}

static void setShapePosition(ActiveBlocks* active, int x, int y)
{
	int xOffset = x - (int)active->positions[0].x;
	int yOffset = y - (int)active->positions[0].y;
	for (int i = 0; i < 4; i++)
	{
		active->positions[i].x += xOffset;  // active->positions[i].x * BLOCK_WIDTH;
		active->positions[i].y += yOffset;  // active->positions[i].y * BLOCK_HEIGHT;
	}
}

static void nextShape(TetrisBoard* tetris)
{
	tetris->activeShape = tetris->nextShape;
	setShapePosition(&tetris->activeShape, START_X, START_Y);
	tetris->nextShape = getNextShape((Shapes)getRandomShapeId(), NEXT_SHAPE_X, NEXT_SHAPE_Y);
}

static void initBoard(TetrisBoard* tetris, memory_arena* arena)
{
	tetris->w = 10;
	tetris->h = 30;

	// tetris->blocks = (Block*)PushArray(arena, tetris->w * tetris->h, int);
	memset(tetris->blocks, -1, ArrayCount(tetris->blocks) * sizeof(Block));
	tetris->activeShape = getNextShape(Shape_I, tetris->w / 2, tetris->h - 1);
	tetris->nextShape = getNextShape((Shapes)getRandomShapeId(), NEXT_SHAPE_X, NEXT_SHAPE_Y);
	// tetris->nextShape = getNextShape((Shapes))

	for (int i = 0; i < tetris->w * tetris->h; i++)
	{
		tetris->blocks[i] = Block_Empty;
	}

	tetris->blocks[0] = Block_Blue;
	tetris->blocks[1] = Block_Blue;
	tetris->blocks[2] = Block_Blue;
}

static void moveDown(TetrisBoard* tetris, int startY)
{
	for (int y = startY; y < tetris->h - 1; y++)
	{
		for (int x = 0; x < tetris->w; x++)
		{
			int index = y * tetris->w + x;
			int upperBlockIndex = (y + 1) * tetris->w + x;

			tetris->blocks[index] = tetris->blocks[upperBlockIndex];
		}
	}

	for (int i = (tetris->w - 1) * tetris->h; i < tetris->w * tetris->h; i++)
	{
		tetris->blocks[i] = Block_Empty;
	}
}

static int checkForTetris(TetrisBoard* tetris)
{
	for (int y = 0; y < tetris->h; y++)
	{
		bool filled = true;
		for (int x = 0; x < tetris->w; x++)
		{
			int index = y * tetris->w + x;

			if (tetris->blocks[index] == Block_Empty)
			{
				filled = false;
				continue;
			}
		}
		if (filled)
		{
			LOGI("Tetris on line: %d", y);
			return y;
		}
	}
	return -1;
}

static inline vec4 getBlockColor(int id)
{
	return BlockColors[id];
}

static void renderActiveShape(ActiveBlocks* active, Layer* renderData)
{
	vec4 uv = { 0.f, 0.f, 1.f, 1.f };
	vec4 color = getBlockColor(active->block);
	vec4 posSize{ 0.f, 0.f, BLOCK_WIDTH, BLOCK_HEIGHT };

	for (int i = 0; i < 4; i++)
	{
		posSize.x = active->positions[i].x * BLOCK_WIDTH;
		posSize.y = active->positions[i].y * BLOCK_HEIGHT;
		renderData->draw(&posSize, &uv, &color);
	}
}

static void renderBoard(TetrisBoard* tetris, Layer* layer)
{
	vec4 uv = { 0.f, 0.f, 1.f, 1.f };
	for (int i = 0; i < tetris->h; i++)
	{
		for (int j = 0; j < tetris->w; j++)
		{
			int index = i * tetris->w + j;
			int block = tetris->blocks[index];

			if (block > 0)
			{
				vec4 color = getBlockColor(block);
				vec4 pos = { j * BLOCK_WIDTH, i * BLOCK_HEIGHT, BLOCK_WIDTH, BLOCK_HEIGHT };
				layer->draw(&pos, &uv, &color);
			}
		}
	}
}

// true jos tiili uudessa sijainnissa
static bool checkPositions(TetrisBoard* tetris, ActiveBlocks* shape, Vec2 offset)
{
	bool result = false;
	for (int i = 0; i < 4; i++)
	{
		int newPosX = (int)(shape->positions[i].x + offset.x);
		int newPosY = (int)(shape->positions[i].y + offset.y);

		int index = newPosY * tetris->w + newPosX;

		if (newPosY < 0)
		{
			result = true;
			break;
		}

		if (newPosX < 0 || newPosX >= tetris->w)
		{
			result = true;
			break;
		}

		if (index < tetris->w * tetris->h)
		{
			int block = tetris->blocks[index];
			if (block != 0)
			{
				result = true;
				break;
			}
		}
	}
	return result;
}

static void move(ActiveBlocks* activeShape, Vec2* move)
{
	for (int i = 0; i < 4; i++)
	{
		activeShape->positions[i].x += move->x;
		activeShape->positions[i].y += move->y;
	}
}

static void moveActiveShape(TetrisBoard* tetris, int directionX, int directionY)
{
	auto* shape = &tetris->activeShape;
	Vec2 moveDir{ (float)directionX, (float)directionY };
	bool blocked = checkPositions(tetris, &tetris->activeShape, moveDir);

	if (!blocked)
	{
		move(shape, &moveDir);
	}
}

static void rotateShape(TetrisBoard* tetris, ActiveBlocks* activeShape)
{
	ActiveBlocks newPosition = *activeShape;

	auto* shape = &tetris->activeShape;
	int xPivot = (int)shape->positions[2].x;
	int yPivot = (int)shape->positions[2].y;

	for (int i = 0; i < 4; i++)
	{
		int xCenter = (int)shape->positions[i].x;
		int yCenter = (int)shape->positions[i].y;

		int newXCenter = xPivot + yPivot - yCenter;
		int newYCenter = yPivot - xPivot + xCenter;

		newPosition.positions[i].x = (float)newXCenter;
		newPosition.positions[i].y = (float)newYCenter;
	}

	// laita uusi activeblocks chekkaamaan collisionit
	Vec2 noOffset{};
	bool blocked = checkPositions(tetris, &newPosition, noOffset);
	if (!blocked)
	{
		*activeShape = newPosition;
	}
}

// 40 (n + 1) 100 * (n+1) 300 * (n+1) 1200 * (n + 1)
static void addScore(TetrisBoard* tetris, int combo)
{
	int baseCoeffs[4]{ 40, 100, 300, 1200 };
	ASSERT(combo < 4 && combo >= 0);
	tetris->score += baseCoeffs[combo] * (tetris->level + 1);
}

static void updateBoard(TetrisBoard* tetris, TickTimer* timer)
{
	// float tickTime = getCurrentTime();
	// if next
	// setup + update()
	//	static float timeUntil = 500.f;
	//	static float nextTick = getCurrentTime() + timeUntil;
	bool stop = false;

	// static Timer timer{ getCurrentTime() + 500.f, 500.f };

	//if (getCurrentTime() > nextTick)
	if (timer->update())
	{
		// nextTick = getCurrentTime() + timeUntil;

		LOGI("TICK\n");
		stop = checkPositions(tetris, &tetris->activeShape, Vec2{ 0, -1 });

		if (!stop)
		{
			Vec2 mov{ 0, -1 };
			move(&tetris->activeShape, &mov);
		}
		else
		{
			for (int i = 0; i < 4; i++)
			{
				int x = (int)tetris->activeShape.positions[i].x;
				int y = (int)tetris->activeShape.positions[i].y;

				int index = y * tetris->w + x;
				tetris->blocks[index] = tetris->activeShape.block;
			}

			// tetris->activeShape = getNextShape((Shapes)getRandomShapeId(), START_X, START_Y);
			nextShape(tetris);

			int comboCount = 0;
			for (int i = 0; i < 5; i++)
			{
				int y = checkForTetris(tetris);
				if (y != -1)
				{
					moveDown(tetris, y);
					++comboCount;
				}
			}

			addScore(tetris, comboCount);
		}
	}
}

void updateTetris(EngineCore* core, TetrisGameState* gameState)
{
	using namespace engine;

	if (Input::isKeyDown(Keycode::D))
	{
		gameState->playerPos.x += 0.5f;
	}
	else if (Input::isKeyDown(Keycode::A))
	{
		gameState->playerPos.x -= 0.5f;
	}
	// static Timer moveRightLeftTimer{ getCurrentTime() + 40.f, 40.f };
	if (Input::isKeyPressed(Keycode::A))
	{
		// moveRightLeftTimer.next = getCurrentTime() + 40.f;
		gameState->timers[0].nextTick = frame + 2;
		moveActiveShape(&gameState->tetris, -1, 0);
	}
	else if (Input::isKeyDown(Keycode::A))
	{
		// move left
		// if (moveRightLeftTimer.update(getCurrentTime()))
		if (gameState->timers[0].update())
			moveActiveShape(&gameState->tetris, -1, 0);
	}

	if (Input::isKeyPressed(Keycode::D))
	{
		// moveRightLeftTimer.next = getCurrentTime() + 40.f;
		gameState->timers[1].nextTick = frame + 2;
		moveActiveShape(&gameState->tetris, 1, 0);
	}
	else if (Input::isKeyDown(Keycode::D))
	{
		// move right
	//	if (moveRightLeftTimer.update(getCurrentTime()))
		if (gameState->timers[1].update())
			moveActiveShape(&gameState->tetris, 1, 0);
	}

	if (Input::isKeyDown(Keycode::S))
	{
		// static Timer moveDownTimer{ getCurrentTime() + 40.f, 40.f };
		// if (moveDownTimer.update(getCurrentTime()))
		if (gameState->timers[2].update())
			moveActiveShape(&gameState->tetris, 0, -1);

		// LOGI("FRAME: %i", core->currentFrame);
		// log(core->log, "%s", "S DOWN");

		LOG("%s", "s down");
	}

	if (Input::isKeyPressed(Keycode::SPACE))
	{
		rotateShape(&gameState->tetris, &gameState->tetris.activeShape);

		// log(core->log, "%s", "spacebar");

		// log(g_log, "%s", "space");
		LOG("%s", "spacebar");
	}

	updateBoard(&gameState->tetris, &gameState->timers[3]);
}

void drawTetris(EngineCore* core, TetrisGameState* gameState)
{
	auto layer = &core->layers[0]; 
	renderActiveShape(&gameState->tetris.activeShape, layer);
	renderActiveShape(&gameState->tetris.nextShape, layer);
	renderBoard(&gameState->tetris, layer);

	TetrisBoard* tetris = &gameState->tetris;
	char buffer[256];

	stbsp_sprintf(buffer, "Score: %i", tetris);
	Vec2 scorePosition{ 300, 420 };
	layer->drawString(buffer, &scorePosition, 0xFFFFFFFF, 0);

	stbsp_sprintf(buffer, "Level: %i", tetris->level);
	Vec2 levelPosition{ 300, 380 };
	layer->drawString(buffer, &levelPosition, 0xFFFFFFFF, 0);
}
