class Font
{
private:
	ftgl::texture_atlas_t* m_FTAtlas;
	ftgl::texture_font_t* m_FTFont;
	unsigned int m_Size;
	Vec2 m_Scale;
	std::string m_Name;
	std::string m_Filename;
public:
	Font(std::string name, std::string filename, int size)
		: m_Name(name), m_Filename(filename), m_Size(size)
	{
		m_FTAtlas = ftgl::texture_atlas_new(512, 512, 2);
		m_FTFont = ftgl::texture_font_new_from_file(m_FTAtlas, (float)size, filename.c_str());
	}

	void setScale(float x, float y) {
		m_Scale = { x, y };
	}

	inline ftgl::texture_font_t* getFTFont() const { return m_FTFont; }

	inline const unsigned int getID() const { return m_FTAtlas->id; }
	inline const Vec2& getScale() const { return m_Scale; }
	inline const std::string& getName() const { return m_Name; }
	inline const std::string& getFileName() const { return m_Filename; }
	inline const int getSize() const { return m_Size; }
};

