constexpr float BENCMARK_ENTITY_SIZE = 16.f;


static void initBenchmark(Benchmark* b)
{
	b->positions = new float[BENCHMARK_ENTITYS_ATTRIBUTE_COUNT];
	b->velocities = new float[BENCHMARK_ENTITYS_ATTRIBUTE_COUNT];

	for (int i = 0; i < 16; i++)
	{
		state[i] = rand() % 1324234;
	}

	for (int i = 0; i < BENCHMARK_ENTITYS_ATTRIBUTE_COUNT; ++i)
	{
		b->positions[i] = RandomFloat(0.f, 600.f);
		b->velocities[i] = RandomFloat(-2.f, 2.f);
	}
}

static void update(Benchmark* b, vec4* cameraRect)
{
	for (int i = 0; i < BENCHMARK_ENTITYS_ATTRIBUTE_COUNT; ++i)
	{
		b->positions[i] += b->velocities[i];

		if (b->positions[i] < cameraRect->x)
		{
			b->velocities[i] *= -1;
			b->positions[i] = cameraRect->x + 1;
		}
		else if (b->positions[i] > cameraRect->w)
		{
			b->velocities[i] *= -1;
			b->positions[i] = cameraRect->w - 1;
		}
	}
}

static void render(Benchmark* b, RenderData* sr, int tex)
{
	sr->setTexture(tex);
	vec4 position{ 0.f, 0.f, BENCMARK_ENTITY_SIZE, BENCMARK_ENTITY_SIZE };
	vec4 uv{ 0.f, 0.f, 1.f, 1.f };
	for (int i = 0; i < BENCHMARK_ENTITYS_ATTRIBUTE_COUNT; i += 2)
	{
		position.x = b->positions[i];
		position.y = b->positions[i + 1];
		// sr->DrawSprite(&position, &uv);
	}
}


