static bool copyFile(const char* filename, const char* newName)
{
	FILE* file = fopen(filename, "rb");

	if (file)
	{
		fseek(file, 0L, SEEK_END);
		long size = ftell(file);
		fseek(file, 0L, SEEK_SET);

		void* mem = malloc(sizeof(char) * size);
		fread(mem, sizeof(char), size, file);

		fclose(file);

		file = fopen(newName, "wb");
		fwrite(mem, sizeof(char), size, file);
		fclose(file);


		free(mem);
		LOGI("success\n");
		return true;
	}
	else
	{
		LOGI("failure\n");
		perror("Error");
		return false;
	}
}

