void showArray(member_definition* member, void* memberPtr, int count)
{
	if (ImGui::TreeNode(member->name)) // blocks
	{
		ImGui::TreePush();

		ImGuiListClipper clipper(count);
		clipper.Begin(count);

		ImGui::NextColumn();
		ImGui::NextColumn();

		while (clipper.Step())
		{
			// ImGui::Text("yolo");
			for (int i = clipper.DisplayStart; i < clipper.DisplayEnd; ++i)
			{
				ImGui::PushID(i);
				dumpToImgui(member, memberPtr, i); // members
				ImGui::PopID();
			}
		}

		ImGui::TreePop();
		ImGui::TreePop();

		ImGui::Separator();
	}
	else
	{
		ImGui::NextColumn();
		ImGui::NextColumn();
	}
	// ImGui::Columns();
	// ImGui::Separator();
	// ImGui::Spacing();
	// ImGui::Spacing();
}

