#define SAVEFILE "assets/data/world.dat"
void serializeGamestate(TetrisGameState* gameState)
{
	FILE* saveFile = fopen(SAVEFILE, "wb");
	if (saveFile)
	{
		fwrite(gameState, sizeof(TetrisGameState), 1, saveFile);
		fclose(saveFile);
	}
	else
	{
		ASSERT(false);
	}
}
