		io::WriteBufferToFile("assets/data/serilizaatio.txt", bufer, strlen(bufer));

		deSerializeStructR(ArrayCount(membersOf_Application), &app2, membersOf_Application, &s, 0);

		char* test = io::ReadEntireFile("assets/data/serilizaatio.txt");
		float* floats = (float*)ptr;

