static void drawPlayer(Layer* layer, TetrisGameState* state)
{
	vec4 uv{ 0.f, 0.f, 1.f, 1.f };
	vec4 color{ 1.0f, 1.0f, 1.0f, 1.0f };
	layer->draw(&state->playerPos, &uv, &color);
}

