	void SpriteRenderer::Init(RenderData* renderData, int maxSize, Shader* shader)
	{
		// nelja vertexsia * float * att
		int quadBufferSize = maxSize * 4 * 3 * sizeof(float);
		int uvsSize = maxSize * 4 * 2 * sizeof(float);
		int indexBufferSize = maxSize * 6 * sizeof(float);


		indices = (GLuint*)malloc(indexBufferSize);
		this->maxSize = maxSize;
		this->maxIndicesSize = maxSize * 6;

		positions = new float[maxSize * 3 * 4]; // 3 per 4
		uvs = new float[maxSize * 2 * 4];
		ids = new float[maxSize * 1 * 4];
		colors = new unsigned int[maxSize * 1 * 4];


		// buffer[ipos] = base[ipos] = positions;
		// buffer[iuv] = base[iuv] = uvs;
		// buffer[iid] = base[iid] = ids;

		renderData->base[ipos] = renderData->buffer[ipos] = positions;
		renderData->base[iuv] = renderData->buffer[iuv] = uvs;
		renderData->base[iid] = renderData->buffer[iid] = ids;

		renderData->baseColor = renderData->bufferColor = colors;


#if __EMSCRIPTEN__
		glGenVertexArraysOES(1, &VAO);
		glBindVertexArrayOES(VAO);            // TODO: laita kaikki samaan niin varmaan toimii selaimessa
#else
		glGenVertexArrays(1, &VAO);
		glBindVertexArray(VAO);
#endif

		glGenBuffers(1, &positionBuffer);

#if !SOA
		glBindBuffer(GL_ARRAY_BUFFER, positionBuffer);
		glBufferData(GL_ARRAY_BUFFER, maxSize * sizeof(VertexDat) * 4, NULL, GL_DYNAMIC_DRAW);
#else
		glEnableVertexAttribArray(0);
		glEnableVertexAttribArray(1);
		glEnableVertexAttribArray(2);
		glEnableVertexAttribArray(3);

		glGenBuffers(1, &POS);
		glBindBuffer(GL_ARRAY_BUFFER, POS);
		glVertexAttribPointer(0, 3, GL_FLOAT, false, 0, 0);
		glBufferData(GL_ARRAY_BUFFER, maxSize * sizeof(float) * 4 * 3, NULL, GL_DYNAMIC_DRAW); // 3 neliötä    4    pistettä            3     positionia      floatteina

		glGenBuffers(1, &UVS);
		glBindBuffer(GL_ARRAY_BUFFER, UVS);
		glVertexAttribPointer(1, 2, GL_FLOAT, false, 0, 0);
		glBufferData(GL_ARRAY_BUFFER, maxSize * sizeof(float) * 4 * 2, NULL, GL_DYNAMIC_DRAW);

		glGenBuffers(1, &ID);
		glBindBuffer(GL_ARRAY_BUFFER, ID);
		glVertexAttribPointer(2, 1, GL_FLOAT, false, 0, 0);
		glBufferData(GL_ARRAY_BUFFER, maxSize * sizeof(float) * 4 * 1, NULL, GL_DYNAMIC_DRAW);

		glGenBuffers(1, &COLOR);
		glBindBuffer(GL_ARRAY_BUFFER, COLOR);
		glVertexAttribPointer(3, 4, GL_UNSIGNED_BYTE, GL_TRUE, 0, 0);
		glBufferData(GL_ARRAY_BUFFER, maxSize * sizeof(unsigned int) * 4 * 1, NULL, GL_DYNAMIC_DRAW);
#endif


#if !SOA
		glVertexAttribPointer(0, 3, GL_FLOAT, false, sizeof(VertexDat), 0);
		glVertexAttribPointer(1, 2, GL_FLOAT, false, sizeof(VertexDat), (const GLvoid*)(offsetof(VertexDat, uv)));
		glVertexAttribPointer(2, 1, GL_FLOAT, false, sizeof(VertexDat), (const GLvoid*)(offsetof(VertexDat, id)));
#else
#endif


		int index = 0;
		for (int i = 0; i < maxSize * 6; i += 6)
		{
			indices[i] = index + 0;
			indices[i + 1] = index + 1;
			indices[i + 2] = index + 2;
			indices[i + 3] = index + 2;
			indices[i + 4] = index + 3;
			indices[i + 5] = index + 0;
			index += 4;
		}
		glGenBuffers(1, &indexBuffer);
		glBindBuffer(GL_ELEMENT_ARRAY_BUFFER, indexBuffer);
		glBufferData(GL_ELEMENT_ARRAY_BUFFER, maxSize * sizeof(GLuint) * 6, indices, GL_STATIC_DRAW);


#if __EMSCRIPTEN__
		glBindVertexArrayOES(0);

		// vertexData = new VertexDat[maxSize * 4];
		// start = vertexData;
#else
		glBindVertexArray(0);

#if MAP_BUFFER
		// vertexData = new VertexDat[maxSize * 4];
		// start = vertexData;
#else
#endif

#endif
        glCheckError();
		glBindBuffer(GL_ELEMENT_ARRAY_BUFFER, 0);
        glCheckError();
		glBindBuffer(GL_ARRAY_BUFFER, 0);
        glCheckError();

        LOGI("inti pah\n");
		glCheckError();
		shader->use();
		glCheckError();
#if 1
		int values[8]{ 0, 1, 2, 3, 4, 5, 6, 7 };
		shader->setInts("textures", 8, values);
		glCheckError();
#else
		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);

		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);
		shader->setInts("textures", 8, values);
#endif

		glCheckError();
		glCheckError();
	}

	void SpriteRenderer::Clear(RenderData* renderData)
	{
		size = 0;
		indSize = 0;
		_currentSize = 0;
		currentSlots = 0;


		for (int i = 0; i < 3; i++)
		{
			renderData->buffer[i] = renderData->base[i];
		}
		renderData->bufferColor = renderData->baseColor;
		renderData->indCount = 0;
		renderData->currentSlots = 0;

		for (int i = 0; i < 32; i++)
		{
			renderData->textureSlots[i] = -1;
		}

		buffer[0] = base[0];
		buffer[1] = base[1];
		buffer[2] = base[2];

#if __EMSCRIPTEN__
		vertexData = start;
#else
#if MAP_BUFFER
		vertexData = start;
#else
		glBindBuffer(GL_ARRAY_BUFFER, positionBuffer);
		vertexData = (VertexDat*)glMapBuffer(GL_ARRAY_BUFFER, GL_WRITE_ONLY);
#endif
#endif
	}


