	inline void SpriteRenderer::DrawSprite(float x, float y, float w, float h, float uv0, float uv1, float uv2, float uv3,
		int texId)
	{
		// if (_currentSize == maxSize)
			// ASSERT(false);
		bool found = false;
		int i = 0;
		float ts = 0.0f;
		for (; i < MAX_TEXTURES; i++)
		{
			if (texId == textureSlots[i])
			{
				ts = (float)(i + 1);
				found = true;
				break;
			}
		}

		if (!found)  // huom loop ^^
		{
			textureSlots[currentSlots] = texId;
			++currentSlots;
			ts = (float)(currentSlots);
		}

		vertexData->position = { x, y + h, 0.0f };
		vertexData->uv = { uv0, uv1 };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { x, y, 0.0f };
		vertexData->uv = { uv0, uv1 + uv3 };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { x + w, y, 0.0f };
		vertexData->uv = { uv0 + uv2, uv1 + uv3 };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { x + w, y + h, 0.0f };
		vertexData->uv = { uv0 + uv2, uv1 };
		vertexData->id = ts;
		vertexData++;

		// texIds
		size += 4;
		indSize += 6;
		_currentSize += 1;

	}

	static float ts = 0.0f;
	void SpriteRenderer::setTexture(int texId)
	{
		bool found = false;
		int i = 0;
		float ts = 0.0f;
		for (; i < MAX_TEXTURES; i++)
		{
			if (texId == textureSlots[i])
			{
				ts = (float)(i + 1);
				found = true;
				break;
			}
		}

		if (!found)  // huom loop ^^
		{
			textureSlots[currentSlots] = texId;
			++currentSlots;
			ts = (float)(currentSlots);
		}
	}

	void SpriteRenderer::DrawSprite2(vec4* __restrict posSize, vec4* __restrict uv, vec4* color)
	{
		// if (indSize == maxIndicesSize)
			// *((int*)0) = 12;
			//ASSERT(false);
		assert(false);
		assert(false);
		assert(false);
		assert(false);
		assert(false);

		int r = (int)(color->x * 255.0f);
		int g = (int)(color->y * 255.0f);
		int b = (int)(color->w * 255.0f);
		int a = (int)(color->h * 255.0f);

		unsigned int c = (a << 24 | b << 16 | g << 8 | r);

		//*(buffer[icolor] + 0) = c;
		//*(buffer[icolor] + 1) = c;
		//*(buffer[icolor] + 2) = c;
		//*(buffer[icolor] + 3) = c;

#if !SOA
		vertexData->position = { posSize->x, posSize->y + posSize->h, 0.0f };
		vertexData->uv = { uv->x, uv->y };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { posSize->x, posSize->y, 0.0f };
		vertexData->uv = { uv->x, uv->y + uv->h };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { posSize->x + posSize->w, posSize->y, 0.0f };
		vertexData->uv = { uv->x + uv->w, uv->y + uv->h };
		vertexData->id = ts;
		vertexData++;

		vertexData->position = { posSize->x + posSize->w, posSize->y + posSize->h, 0.0f };
		vertexData->uv = { uv->x + uv->w, uv->y };
		vertexData->id = ts;
		vertexData++;
#else
		*(buffer[ipos] + 0) = posSize->x;
		*(buffer[ipos] + 1) = posSize->y + posSize->h;
		*(buffer[ipos] + 2) = 0.0f;

		*(buffer[ipos] + 3) = posSize->x;
		*(buffer[ipos] + 4) = posSize->y;
		*(buffer[ipos] + 5) = 0.0f;

		*(buffer[ipos] + 6) = posSize->x + posSize->w;
		*(buffer[ipos] + 7) = posSize->y;
		*(buffer[ipos] + 8) = 0.0f;

		*(buffer[ipos] + 9) = posSize->x + posSize->w;
		*(buffer[ipos] + 10) = posSize->y + posSize->h;
		*(buffer[ipos] + 11) = 0.0f;

		buffer[ipos] += 12;

		*(buffer[iuv] + 0) = uv->x;
		*(buffer[iuv] + 1) = uv->y;

		*(buffer[iuv] + 2) = uv->x;
		*(buffer[iuv] + 3) = uv->y + uv->h;

		*(buffer[iuv] + 4) = uv->x + uv->w;
		*(buffer[iuv] + 5) = uv->y + uv->h;

		*(buffer[iuv] + 6) = uv->x + uv->w;
		*(buffer[iuv] + 7) = uv->y;

		buffer[iuv] += 8;

#if 1
//		memset(buffer[iid], ts, 4 * sizeof(float));
#else
		*(buffer[iid] + 0) = ts;
		*(buffer[iid] + 1) = ts;
		*(buffer[iid] + 2) = ts;
		*(buffer[iid] + 3) = ts;
#endif
		buffer[iid] += 4;
#endif
		indSize += 6;
		glCheckError();
	}

	void SpriteRenderer::DrawSpriteF(float x, float y, float w, float h, float uv0, float uv1, float uv2, float uv3, int texId, bool flippedX, bool flippedY)
	{
		if (flippedX)
		{
			x += w;
			w = -w;
		}

		if (flippedY)
		{
			y += h;
			h = -h;
		}

		DrawSprite(x, y, w, h, uv0, uv1, uv2, uv3, texId);
	}



	void SpriteRenderer::drawString(const char* text, int count, Vec2* position, const Font& font,
		unsigned int color, RenderData* data)
	{
		using namespace ftgl;

		float ts = 0.0f;
		bool found = false;
		for (int i = 0; i < MAX_TEXTURES; i++)
		{
			if (data->textureSlots[i] == font.getID())
			{
				ts = (float)(i + 1);
				found = true;
				break;
			}
		}

		if (!found)
		{
			// if ( >= 32)
			// {
				// end();
				// flush();
				// begin();
			// }
			data->textureSlots[data->currentSlots] = font.getID();
			(data->currentSlots) = data->currentSlots + 1;
			ts = (float)(data->currentSlots);
			// m_TextureSlots.push_back(font.getID());
			// ts = (float)(m_TextureSlots.size());
		}

		const Vec2 scale = font.getScale();

		float x = position->x;

		texture_font_t* ftFont = font.getFTFont();

		for (int i = 0; i < count; i++)
		{
			char c = text[i];
			texture_glyph_t* glyph = texture_font_get_glyph(ftFont, c);
			if (glyph != NULL)
			{

				if (i > 0)
				{
					float kerning = texture_glyph_get_kerning(glyph, text[i - 1]);
					x += kerning / scale.x;
				}

				float x0 = x + glyph->offset_x / scale.x;
				float y0 = position->y + glyph->offset_y / scale.y;
				float x1 = x0 + glyph->width / scale.x;
				float y1 = y0 - glyph->height / scale.y;

				float u0 = glyph->s0;
				float v0 = glyph->t0;
				float u1 = glyph->s1;
				float v1 = glyph->t1;


				*(data->bufferColor + 0) = color;
				*(data->bufferColor + 1) = color;
				*(data->bufferColor + 2) = color;
				*(data->bufferColor + 3) = color;
				data->bufferColor += 4;


				*(data->buffer[ipos] + 0) = x0;
				*(data->buffer[ipos] + 1) = y0;
				*(data->buffer[ipos] + 2) = 0.0f;

				*(data->buffer[ipos] + 3) = x0;
				*(data->buffer[ipos] + 4) = y1;
				*(data->buffer[ipos] + 5) = 0.0f;

				*(data->buffer[ipos] + 6) = x1;
				*(data->buffer[ipos] + 7) = y1;
				*(data->buffer[ipos] + 8) = 0.0f;

				*(data->buffer[ipos] + 9) = x1;
				*(data->buffer[ipos] + 10) = y0;
				*(data->buffer[ipos] + 11) = 0.0f;

				data->buffer[ipos] += 12;

				*(data->buffer[iuv] + 0) = u0;
				*(data->buffer[iuv] + 1) = v0;

				*(data->buffer[iuv] + 2) = u0;
				*(data->buffer[iuv] + 3) = v1;

				*(data->buffer[iuv] + 4) = u1;
				*(data->buffer[iuv] + 5) = v1;

				*(data->buffer[iuv] + 6) = u1;
				*(data->buffer[iuv] + 7) = v0;

				data->buffer[iuv] += 8;

				// memset(data->buffer[iid], (int)ts, 4 * sizeof(float));
				*(data->buffer[iid] + 0) = ts;
				*(data->buffer[iid] + 1) = ts;
				*(data->buffer[iid] + 2) = ts;
				*(data->buffer[iid] + 3) = ts;

				data->buffer[iid] += 4;
				data->indCount += 6;

				x += glyph->advance_x / scale.x;
			}
		}
		// LOGI("%s :\n", string.c_str());;
	}




	void SpriteRenderer::RenderLayer(Shader* shader, glm::mat4 cam, Layer* layer)
	{
		Render(shader, &cam, layer->data);
	}



	void SpriteRenderer::Render(Shader* shader, glm::mat4* cam, RenderData* renderData) {


		// #if __EMSCRIPTEN__ || MAP_BUFFER
		buffer[ipos] = renderData->buffer[ipos];
		buffer[iuv] = renderData->buffer[iuv];
		buffer[iid] = renderData->buffer[iid];

		base[ipos] = renderData->base[ipos];
		base[iuv] = renderData->base[iuv];
		base[iid] = renderData->base[iid];


#if !SOA
		glBindBuffer(GL_ARRAY_BUFFER, positionBuffer);

		glBufferData(GL_ARRAY_BUFFER, (vertexData - start) * sizeof(VertexDat), start, GL_DYNAMIC_DRAW);
#else
		glCheckError();
		glBindBuffer(GL_ARRAY_BUFFER, POS);
		glBufferData(GL_ARRAY_BUFFER, (buffer[ipos] - base[ipos]) * sizeof(float), base[ipos], GL_DYNAMIC_DRAW);

		glBindBuffer(GL_ARRAY_BUFFER, UVS);
		glBufferData(GL_ARRAY_BUFFER, (buffer[iuv] - base[iuv]) * sizeof(float), base[iuv], GL_DYNAMIC_DRAW);

		glBindBuffer(GL_ARRAY_BUFFER, ID);
		glBufferData(GL_ARRAY_BUFFER, (buffer[iid] - base[iid]) * sizeof(float), base[iid], GL_DYNAMIC_DRAW);

		glCheckError();
		glBindBuffer(GL_ARRAY_BUFFER, COLOR);
		glBufferData(GL_ARRAY_BUFFER, (renderData->bufferColor - renderData->baseColor) * sizeof(unsigned int), renderData->baseColor, GL_DYNAMIC_DRAW);

		renderData->buffer[ipos] = renderData->base[ipos];
		renderData->buffer[iuv] = renderData->base[iuv];
		renderData->buffer[iid] = renderData->base[iid];
		renderData->bufferColor = renderData->baseColor;
		// buffer[ipos] = base[ipos];
		// buffer[iuv] = base[iuv];
		// buffer[iid] = base[iid];
#endif
		// glBufferData(GL_ARRAY_BUFFER, 0 (vertexData - start) * sizeof(VertexDat), start);

		// glBufferData(GL_ARRAY_BUFFER, (vertexData - start) * sizeof(VertexDat), NULL, GL_DYNAMIC_DRAW);
		// glBufferSubData(GL_ARRAY_BUFFER, 0, (vertexData - start) * sizeof(VertexDat), start);

		// vertexData = start; what
// #else
		// glUnmapBuffer(GL_ARRAY_BUFFER);
// #endif
		// glBindBuffer(GL_ARRAY_BUFFER, 0);

		// glActiveTexture(GL_TEXTURE0);
		// glBindTexture(GL_TEXTURE_2D, texture->getTextureId());
		shader->use();
		shader->setMat4("position", *cam);


#if __EMSCRIPTEN__
		glBindVertexArrayOES(VAO);
#else
		glBindVertexArray(VAO);
#endif

		for (int i = 0; i < MAX_TEXTURES; i++)
		{
			if (renderData->textureSlots[i] == -1)
				break;

			glActiveTexture(GL_TEXTURE0 + i);
			glBindTexture(GL_TEXTURE_2D, renderData->textureSlots[i]);
		}

		glBindBuffer(GL_ELEMENT_ARRAY_BUFFER, indexBuffer);
		glDrawElements(GL_TRIANGLES, renderData->indCount, GL_UNSIGNED_INT, 0);


#if __EMSCRIPTEN__
		glBindVertexArrayOES(0);
#else
		glBindVertexArray(0);
#endif
		glBindBuffer(GL_ELEMENT_ARRAY_BUFFER, 0);

		glCheckError();
	}

