namespace engine {
	class SpriteRenderer
	{
	public:
		void Init(RenderData* renderData, int maxSize, Shader* shader);

		~SpriteRenderer() { free(indices); free(texUVs); free(quads); }

		void Clear(RenderData* renderData);

		inline void DrawSprite(float x, float y, float w, float h, 
			float uv0, float uv1, float uv2, float uv3, int texId);
	
		void setTexture(int texId);


		void RenderLayer(Shader* shader, glm::mat4 cam, Layer* layer);
		void DrawSprite2(vec4* __restrict a, vec4* __restrict b, vec4* color);

		void DrawSpriteF(float x, float y, float w, float h, float uv0, float uv1, float uv2, float uv3,
			int texId, bool flippedX, bool flippedY);

		void Render(Shader* shader, glm::mat4* cam, RenderData* renderData);

		void drawString(const char* text, int count, Vec2* position, const Font& font,
			unsigned int color, RenderData* data);

	private:
		GLuint*	   indices;
		glm::vec2* texUVs;
		glm::vec3* quads;
		GLfloat*     texIds;
		int _currentSize = 0;
		int maxSize;
		int maxIndicesSize;
		int size = 0;
		int indSize = 0;
	};
}
