#include <platform.h>

#define STB_IMAGE_IMPLEMENTATION
#include <3rdparty/stb_image.h>

namespace engine
{
	Texture2D* getTexture(Resources* resources, int id)
	{
		if (!resources->loaded[id])
		{
			int width, height, nrChannels;
			unsigned char *data = stbi_load(ResourcePaths[id], &width, &height, &nrChannels, 0);

			if (data)
			{
				resources->textures[id] = resources->loadTexturePtr(width, height, nrChannels, data);
				stbi_image_free(data);
			}
			else
			{
				printf("failed to load image %s \n", ResourcePaths[id]);

				unsigned char pixels[3] = // Placeholder texture
				{
					255, 0, 255,
				};
				resources->textures[id] = resources->loadTexturePtr(1, 1, 3, pixels);
			}

			resources->loaded[id] = true;
		}
		return &resources->textures[id];
	}
}
