#pragma once
#include <glm/glm.hpp>
#include <glm/gtc/matrix_transform.hpp>
// #include <core/Export.h>

namespace engine {

	class Camera2D
	{
	public:
		Camera2D();
		~Camera2D();

		//laittaa orthographic matrixsin ja ruutu dimensionit kohdilleen
		void init(int screenWidth, int screenHeight);

		void update(); 

		glm::vec2 convertScreenToWorld(glm::vec2 screenCoords);

		bool isBoxinView(const glm::vec2 &position, const glm::vec2 &dimensions);

		void mvoe(const glm::vec2& translate) { _needsMatrixUpdate = true; _position += translate; }
		void setPosition(const glm::vec2 &newPosition) { _position = newPosition; _needsMatrixUpdate = true; }
		void setScale(float newScale) { _scale = newScale; _needsMatrixUpdate = true; }

		void setDirty() { _needsMatrixUpdate = true; }

		void setPositionLua(float x, float y) { setPosition({ x, y }); };
		void moveLua(float x, float y) { mvoe({ x, y }); };

		void zoom(float factor);

		float getScale() { return _scale; }
		glm::vec2 getPosition() { return _position; }
		glm::mat4 getCameraMatrix() { return _cameraMatrix; }

		vec4 getCameraRect();

		float _scale;
		float angle;
		float pitch;
		float yaw;
		glm::vec2 _position;  
	private:
		int _screenWidth, _screenHeight;
		bool _needsMatrixUpdate;
		glm::mat4 _cameraMatrix;
		glm::mat4 _orthoMatrix;
	};
}