bool isWall(char id)
{
	return id == '#';
}

bool isGoal(char id)
{
	return id == 'G';
}

void GetTileFromWorldPosition(vec2f* collides, vec2f pos, int* index)
{
	int realX = (int)((pos.x / tile_size));
	int realY = (int)((pos.y / tile_size));
	int i = realX + realY * LEVEL_SIZE;

	if (i < 0 || i > LEVEL_SIZE * LEVEL_SIZE)
	{
		return;
	}

	char id = level[i]; // collisions

	// printf("(pos: %f, %f):(i (%i, %i))id is %i\n", pos.x, pos.y, realX, realY, id);
	if (isWall(id))
	{
		collides[*index] = { realX * tile_size + tile_size / 2.f, realY * tile_size + tile_size / 2.f };
		(*index)++;
	}
}

