static bool inline isVirus(char c)
{
	return c == 'V';
}

static bool inline isStart(char c)
{
	return c == 'P';
}

static void parseMap(JamState* state, char level[LEVEL_SIZE * LEVEL_SIZE], char* data)
{
	int k = 0;
	for (int i = 0; i < LEVEL_SIZE * LEVEL_SIZE; ++i)
	{
		if (data[k] != '\n')
		{
			level[i] = data[k];
			if (isVirus(data[k]))
			{
				int x = i % LEVEL_SIZE;
				int y = (i - x) / LEVEL_SIZE;
				vec4 position{ x * tile_size, y * tile_size, tile_size, tile_size };
				createEnt(state, &position, Entity_Bacterio);
			}
			else if (isGoal(data[k]))
			{
				int x = i % LEVEL_SIZE;
				int y = (i - x) / LEVEL_SIZE;
				state->goalX = x;
				state->goalY = y;
			}
			else if (isStart(data[k]))
			{
				int x = i % LEVEL_SIZE;
				int y = (i - x) / LEVEL_SIZE;
				state->playerX = x;
				state->playerY = y;
			}
			// LOGI("newLine: %i %i\n", i, k);
#ifdef __EMSCRIPTEN__
			// --i;
			// ++k;
#endif
		}
		else
		{
			--i;
		}
		++k;
	}
}


