static void drawMap(EngineCore* core, char level[LEVEL_SIZE * LEVEL_SIZE], Layer* layer)
{
	// layer->setTexture() // wall tile
	layer->setTexture(engine::getTexture(core->resources, textures::Texture_Tile)->ID);
	vec4 defaultUV{ 0.f, 0.f, 1.f, 1.f };
	vec4 defaultColor{ 1.f, 1.f, 1.f, 1.f };

	for (int i = 0; i < LEVEL_SIZE * LEVEL_SIZE; ++i)
	{
		{
			switch (level[i])
			{
			case 'P': // player start position aka empty
			case 'S': // spawner start aka empty
			case '-': // empty
			case 'V': // virust start
				break;
			case '#': // wall
			{
				int x = i % LEVEL_SIZE;
				int y = (i - x) / LEVEL_SIZE;
				vec4 posSize{ x * tile_size, y * tile_size, tile_size, tile_size };

				layer->draw(&posSize, &defaultUV, &defaultColor);
				// graphics::drawBox(core->lines, &posSize); // wall !!!
			} break;
			default:
				break;
			}
		}
	}
}

void tileCollision(JamEntity* entitys)
{
	vec2f collides[4];

	for (int i = 0; i < entitys->count; i++)
	{
		// if no collision
		//	continue;

		int count = 0;
		vec4* firstPos = &positionSize[i];

		// check tiles positions
		float halfW = std::abs(firstPos->w * 0.5f);
		float halfH = std::abs(firstPos->h * 0.5f);

		vec2f corner0 = { firstPos->x, firstPos->y };   // TODO: W VAI H
		vec2f corner1 = { firstPos->x + halfW * 2, firstPos->y };
		vec2f corner2 = { firstPos->x, firstPos->y + halfW * 2 };
		vec2f corner3 = { firstPos->x + halfW * 2, firstPos->y + halfW * 2 };

		GetTileFromWorldPosition(collides, corner0, &count);
		GetTileFromWorldPosition(collides, corner1, &count);
		GetTileFromWorldPosition(collides, corner2, &count);
		GetTileFromWorldPosition(collides, corner3, &count);

		static const float TILE_RADIUS = tile_size / 2.f;
		const float MIN_DISTANCE = halfW + TILE_RADIUS;

		for (int j = 0; j < count; j++)
		{
			vec2f centerEntityPos{ firstPos->x + halfW, firstPos->y + halfH };
			vec2f distVec = centerEntityPos - collides[j];

			float xDepth = MIN_DISTANCE - abs(distVec.x);
			float yDepth = MIN_DISTANCE - abs(distVec.y);

			if (xDepth > 0 && yDepth > 0)
			{
				if (std::max(xDepth, 0.0f) < std::max(yDepth, 0.0f))
				{
					if (distVec.x < 0.0f)
					{
						(*firstPos).x -= xDepth;
					}
					else
					{
						(*firstPos).x += xDepth;
					}
					// kimpoaminen x ?
					entitys[i].Bacterio.moveDirection.x = -entitys[i].Bacterio.moveDirection.x;
				}
				else
				{
					if (distVec.y < 0.0f)
					{
						(*firstPos).y -= yDepth;
					}
					else
					{
						(*firstPos).y += yDepth;
					}
					entitys[i].Bacterio.moveDirection.y = -entitys[i].Bacterio.moveDirection.y;
				}
			}
		}
	}
}

float g_currentTime = 0.f;
float nextBacteriaMultiply()
{
	return g_currentTime + defaultMultiplyTimer;
}

void createEnt(JamState* state, vec4* posSize, Entity_Enum type)
{
	JamEntity* ents = state->jamEntitys;

	if (ents->count >= MAX_ENTITY)
		return;

	int newID = state->jamEntitys->count;
	state->jamEntitys[newID].type = type;
	positionSize[newID] = *posSize;

	switch (type)
	{
	case Entity_Player:
		break;
	case Entity_Bacterio:
	{
		auto Bacterio = &ents[newID].Bacterio;
		Bacterio->multiplyTimer = nextBacteriaMultiply();
		Bacterio->moveDirection = { RandomFloat(-1.0f, 1.0f), RandomFloat(-1.f, 1.f) };
		Bacterio->moveDirection.safeNormalizeInPlace();

		// heh.
		positionSize[newID].w = RandomFloat(20.f, 40.f);
		positionSize[newID].h = RandomFloat(20.f, 40.f);
	} break;
	default:
		break;
	}

	++ents->count;
}

